<template>
  <div class="wrapper">
    <div class="w-seach" @click="handlePopShow">
      <p :title="curName.length>6?curName:''">{{curName}}</p>
    </div>
    <transition name="fade">
      <depSeach v-if="popShow" :name="curName" @change="popClick"></depSeach>
    </transition>
    <div class="content">
      <div class='c-left'>
        <ctitle :title="'参评情况'"></ctitle>
        <div class='cpl'>
          <liquidfill :params='cplParams'></liquidfill>
          <div class='cpl-font'>参评率</div>
        </div>
        <div class='cpl cpl3d'>
          <customized-pie :params="cpqkPieParams"></customized-pie>
        </div>
      </div>
      <div class='c-center'>
        <ctitle :title="'评价情况'"></ctitle>
        <div class='cpl'>
          <liquidfill :params='mylParams'></liquidfill>
          <div class='cpl-font'>满意率</div>
        </div>
        <div class='cpl cpl3d'>
          <customized-pie :params="pjqkPieParams"></customized-pie>
        </div>
      </div>
      <div class='c-right'>
        <ctitle :title="'市级信访部门满意率排名TOP10'"></ctitle>
        <div class='cpl'>
          <div class="w-echart" id="w-echart" ref="barEl">
            <PaimBar :params="sjxfBarParams"></PaimBar>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {get_http} from "@/api/index";
  import PaimBar from "@/components/bar/PaimBar";
  import ctitle from "@/components/title/index";
  import liquidfill from "@/components/liquidfill/Liquidfill";
  import CustomizedPie from '@/components/pie/CustomizedPie';
  import depSeach from "@/components/depSeach/index";
  import sjpmBtn from "@/components/sjpmBtn/sjpmBtn";
  export default {
    name: "mydjk",
    components: {
      ctitle,
      liquidfill,
      CustomizedPie,
      depSeach,
      PaimBar,
      sjpmBtn
    },
    data() {
      return {
        // 市级信访部门满意率排名
        sjxfBarParams: {
          data: [],
          canvasStyle: {
            width: "80%",
            height: "100%"
          },
        },
        popShow: false,
        loadFlag: false,
        curName: '部门索引',
        // 参评率
        cplParams:{
          data: [],
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options:{
            radius: "50%",
          }
        },
        // 满意率
        mylParams:{
          data: [],
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options:{
            radius: "50%",
          }
        },
        // 参评情况
        cpqkPieParams: {
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options: {
            colors:['rgb( 154, 5, 245 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )','rgb( 235, 131, 124 )'],   //环形颜色
            unti: '件',
            labelColor: '#fff',
            labelFontSize: 16, //21
            labelLineSmooth: 0,
            labelLineHeight: 23,
            radius: '80%'
          },
          data: [
              {value: 70, name: '已评价'},
              {value: 12, name: '超期未评价'},
              {value: 18, name: '待评价'}
          ].sort(function (a, b) { return a.value - b.value; }),
        },

        // 评价情况
        pjqkPieParams: {
          canvasStyle: {
            width: '100%',
            height: '100%'
          },
          options: {
            colors:['rgb( 154, 5, 245 )','rgb( 235, 131, 124 )','rgb( 68, 240, 233 )','rgb( 12, 129, 254 )',],   //环形颜色
            unti: '件',
            labelColor: '#fff',
            labelFontSize: 16, //21
            labelLineSmooth: 0,
            labelLineHeight: 23,
            radius: '80%'
          },
          data: [
              {value: 70, name: '已评价'},
              {value: 12, name: '超期未评价'},
              {value: 18, name: '待评价'}
          ].sort(function (a, b) { return a.value - b.value; }),
        },
      };
    },
    mounted() {
      this.getAPI(this.date, this.curName);
    },
    methods: {
      handlePopShow() {
        this.popShow = true;
      },
      // 点击弹框后触发
      popClick(name){
        this.curName=(name === ''?'部门索引':name);
        this.getAPI(this.date, this.curName);
      },
      getAPI(date, curName) {
        // 2.参评情况+参评率&main_name=已评价,超期未评价
        get_http("qjywjkMydjkXfbmCpqk", {
          main_name: '已评价,超期未评价',
          xfbm: curName,
          day: `[${date.startDate},${date.endDate}]`,
          full: true
        })
        .then(res => {
          this.cpqkPieParams.data = res.data.data;
           //总数
          let total = this.cpqkPieParams.data.reduce((acc, cur) => {
            return acc + cur.value
          },0)
          let myData = this.cpqkPieParams.data.filter(item => {
            return item.name === '已评价'
          })
          // 已评价的值
          let ypjValue = myData[0].value;
          console.log('6789', myData)
          // 率
          let proportion = ypjValue/total;
          this.cplParams.data = [{value: proportion}]
        })
        .catch(err => {
          console.log(err);
        });
        
        // 4.评价情况+满意率main_name=满意,不满意,基本满意,超期未评价
        get_http("qjywjkMydjkXfbmPjqk", {
          main_name: '满意,不满意,基本满意',
          xfbm: curName,
          day: `[${date.startDate},${date.endDate}]`,
          full: true
        })
        .then(res => {
          this.pjqkPieParams.data = res.data.data;
          // 总数
          let total = this.pjqkPieParams.data.reduce((acc, cur) => {
            return acc + cur.value
          },0)
          // 过滤数据
          let myData = this.pjqkPieParams.data.filter(item => {
            return item.name !== '不满意'
          })
          // 满意+基本满意总数
          let mylValue = myData.reduce((acc, cur) => {
            return acc + cur.value
          },0)
          let proportion = mylValue/total
          this.mylParams.data = [{value: proportion}]
        })
        .catch(err => {
          console.log(err);
        });
        this.getSjxfAPI(date)
      },
      getSjxfAPI(data) {
        // 5.市级信访部门满意率排名
        get_http("qjywjkMydjkXfbmSjxfpm", {data: data})
          .then(res => {
            this.sjxfBarParams.data = res.data.data.sort((a, b) => a.value - b.value);
          })
          .catch(err => {
            console.log(err);
          });
      },
      fontSize(res) {
        let docEl = document.documentElement,
          clientWidth = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
        if (!clientWidth) return;
        let fontSize = 100 * (clientWidth / 1920);
        return res * fontSize;
      },
    },
    computed: {
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      date(val) {
        this.getAPI(val, this.curName);
      }
    }
  };
</script>

<style lang="stylus" scoped>
  .wrapper
    position relative
    top 0.8rem
    height calc(100% - 0.8rem)
    .w-seach
      position absolute
      top -78px
      right 113px
      z-index 1000
      box-sizing border-box
      padding-left 42px
      width 270px
      line-height 70px
      font-size 22px
      color #cbfbff
      background url("../../../assets/seach-bg3.png") no-repeat
      background-size 100% 100%
      cursor pointer
      p
        width 150px
        text-align center
        white-space nowrap
        overflow hidden
        text-overflow ellipsis
    .w-popup
      position fixed
      top 0
      left 0
      right 0
      bottom 0
      background rgba(0, 0, 0, 0.4)
      z-index 1005
      font-size 0
    .content
      width 100%
      height 100%
      display flex
      flex-direction row
      padding .2rem .3rem
      box-sizing border-box
      justify-content space-around
      .c-left .c-center .c-right
        display flex
        justify-content center
        box-sizing border-box
      .c-left
        flex 1
        border-right 1px #fff dashed
        display flex
        flex-direction column
        margin-top 0.2rem
        .cpl
          display flex
          flex 1
          justify-content center
          flex-direction center
          position relative
          .cpl-font
            position absolute
            bottom 0.4rem
            text-align center
            color #ffffff
            font-size 0.2rem
        .cpl3d
          width 100%
          div
            width 100%
            height 100%
      .c-center
        flex 1
        display flex
        flex-direction column
        border-right 1px #fff dashed
        margin-top 0.2rem
        .cpl
          display flex
          flex 1
          justify-content center
          flex-direction center
          position relative
          .cpl-font
            position absolute
            bottom 0.4rem
            text-align center
            color #ffffff
            font-size 0.2rem
        .cpl3d
          width 100%
          div
            width 100%
            height 100%
      .c-right
        flex 1
        display flex
        flex-direction column
        .cpl
          display flex
          flex 1
          flex-direction column
          margin-top 0.1rem
        .w-echart
          margin-top 0.3rem
          justify-content center
          display flex
          flex: 1
      </style>
